const NODE_ENV = process.env.NODE_ENV
console.log(process.env)
const config = {
  production: {
    packageType: '',
    baseInfo: {
      chatDomain: 'http://localhost:8050/chat/f',
      domain: 'http://www.wpdev.com',
      imagePath: 'http://image.wpdev.com'
    }
  },
  development: {
    host: 'localhost',
    port: 8081,
    /**
     * target 要代理的域名
     * changeOrigin 允许跨域
     * pathRewrite 定义要访问的路径
     */
    proxyTable: {
      '/api': {
        target: 'http://192.168.0.1:200',
        changeOrigin: true,
        pathRewrite: {
          '^/api': ''
        }
      }
    },
    baseInfo: {
      chatDomain: 'http://localhost:8050/chat/f',
      domain: 'http://www.wpdev.com',
      imagePath: 'http://image.wpdev.com'
    }
  }
}
module.exports = config[NODE_ENV] || config['development']



